import './menu.scss'

export const Menu = () => {
  return (
    <nav className='menu'>
      <ul className="menu__list">
       <li className="menu__item">
          <a href="$" className="menu__link" title=' ' onClick={e => e.preventDefault()}> </a>
        </li>
        <li className="menu__item">
          <a href="$" className="menu__link" title='  ' onClick={e => e.preventDefault()}>  </a>
        </li>
        <li className="menu__item">
          <a href="$" className="menu__link" title='  ' onClick={e => e.preventDefault()}>  </a>
        </li>
       <li className="menu__item">
          <a href="$" className="menu__link" title='' onClick={e => e.preventDefault()}></a>
        </li>
       <li className="menu__item window">
          <a href="$" className="menu__link" title=' ' onClick={e => e.preventDefault()}> </a>
          <ul className="window__list">
            <li className="window__item">
              <a href="$" className="window__link" title=' ' onClick={e => e.preventDefault()}> </a></li>
            <li className="window__item"><a href="$" className="window__link" title='' onClick={e => e.preventDefault()}></a></li>
            <li className="window__item"><a href="$" className="window__link" title='' onClick={e => e.preventDefault()}></a></li>
          </ul>
          <span className='window__triangle'></span>
        </li>
      </ul>
    </nav>
  )
}